<template>
    <el-dialog v-dialogDrag center :title="isUpdate === true ? '修改学生信息' : '添加学生'" :visible.sync="visible" width='30%' @close='delDialog'>
        <el-form :model="formValue" label-width="80px" size='small' :rules="rules" ref="formValue">
            <el-form-item label="学号" prop="stuId">
                <el-input v-model.number="formValue.stuId" :disabled="isUpdate" placeholder="仅支持数值类型"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="stuName">
                <el-input v-model="formValue.stuName"></el-input>
            </el-form-item>
            <el-form-item label="账号" prop="stuUserId">
                <el-input v-model="formValue.stuUserId" :disabled="isUpdate" ></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="stuPas">
                <el-input v-model="formValue.stuPas"></el-input>
            </el-form-item>
            <el-form-item label="所属宿舍" prop="dormId">
                <el-select v-model="formValue.dormId" placeholder="请选择学生所属宿舍">
                    <el-option v-for="item in dormData" :key="item.id" :label="item.dormId" :value="item.id" ></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="delDialog">取 消</el-button>
            <el-button type="primary" @click="confirm('formValue')">确 定</el-button>
        </div>
        </el-dialog>
</template>
    
<script>
import tableMixins from '../../mixins/tableMixin'
export default {
    name: '',
    mixins: [tableMixins],
    props:[
        'type','visible','inputVal'
    ],
    data() {
        return {
            formValue:{
                id:'',
                stuId:'',
                stuName:'',
                stuUserId:'',
                stuPas:'',
                dormId:'',
                ordDorm_DormID:'',
                ordDorm_Id:'',
            },//表单数据
            dormData:[],
            isUpdate:false,//是否为修改弹框
            rules: {
                stuId: [
                    { required: true, message: '学号不能为空', trigger: 'blur' },
                    // { type: 'number', message: '编号必须为数字值'}
                ],
                stuName: [
                    { required: true, message: '学生姓名不能为空', trigger: 'blur' }
                ],
                stuUserId: [
                    { required: true, message: '学生账号不能为空', trigger: 'blur' }
                ],
                stuPas: [
                    { required: true, message: '学生密码不能为空', trigger: 'blur' }
                ],
                dormId: [
                    { required: true, message: '学生所属寝室不能为空', trigger: 'blur' },
                    // { type: 'number', message: '余额必须为数字值'}
                ]
            }
        }
    },
    // computed: {
        
    // },
    created() {
        this.onSelect({
            url:'/dorm/getdorm'
        },(data)=>{
            console.log('获取所有宿舍',data);
            this.dormData = data
        })
    },
    mounted(){
        // console.log('this.dormData',this.dormData)
    },
    watch:{
        inputVal(){
            console.log('inputVal发生了变化',this.inputVal);
            this.formValue.id = this.inputVal.id
            this.formValue.stuId = this.inputVal.stuId
            this.formValue.stuName = this.inputVal.stuName
            this.formValue.stuUserId = this.inputVal.stuUserId
            this.formValue.stuPas = this.inputVal.stuPas
            this.formValue.dormId = this.inputVal.dormId
            this.formValue.ordDorm_DormID = this.inputVal.dormId
            this.formValue.ordDorm_Id = this.inputVal.stuDormId
            this.isUpdate = true
        },
    },
    methods: {
        /* 确定 */
        confirm(formName){
            const _this = this
            _this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(_this.isUpdate){
                        //修改确定
                        console.log('this.formValue.dormId',this.formValue.dormId);
                        console.log('this.formValue.ordDorm_DormID',this.formValue.ordDorm_DormID);
                        console.log('this.formValue.ordDorm_Id',this.formValue.ordDorm_Id);
                        if (this.formValue.dormId === this.formValue.ordDorm_DormID) {
                            this.formValue.dormId = this.formValue.ordDorm_Id
                        }
                        _this.update({
                            url: '/student/updatestudent',
                            data: {
                                id: _this.formValue.id, 
                                stuName: _this.formValue.stuName, 
                                stuPas: _this.formValue.stuPas, 
                                stuDormId: _this.formValue.dormId, 
                            },
                            updateUrl:'/student/getstudent',
                            callback:function(data) {
                                _this.$emit('updatemyData',data)
                                _this.delDialog()
                            }
                        })
                    }else{
                        //增加确定
                        _this.add({
                            url: '/student/addstudent',
                            data: {
                                stuId: _this.formValue.stuId, 
                                stuName: _this.formValue.stuName, 
                                stuUserId: _this.formValue.stuUserId, 
                                stuPas: _this.formValue.stuPas, 
                                stuDormId: _this.formValue.dormId, 
                            },
                            updateUrl:'/student/getstudent',
                            callback:function(data) {
                                // console.log('回调函数中的data',data);
                                _this.$emit('updatemyData',data)
                                _this.delDialog()
                            }
                        })
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            
        },
 
        /* 跟新确定 */
        updateQueding(row) {
            this.updateState = false
            this.update({
                url: '/suShe/gengXinById',
                data: this.formUpdate,
                method: 'post'
            })
        },
         /* 取消弹框 */
        delDialog(){
            // console.log('取消弹框');
            this.isUpdate = false
            this.formValue.id = ''
            this.formValue.stuId = ''
            this.formValue.stuName = ''
            this.formValue.stuUserId = ''
            this.formValue.stuPas = ''
            this.formValue.dormId = ''
            this.$emit('closeDialog')
        },
    
    },
   
}
</script>
