<template>
    <el-dialog v-dialogDrag center title="充值" :visible.sync="visible" width='30%' @close='delDialog'>
        <el-form :model="formValue" label-width="80px" size='small' :rules="rules" ref="formValue">
            <el-form-item label="充值金额" prop="money">
                <el-input v-model.number="formValue.money" placeholder="仅支持数值类型,单位（元）"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="delDialog">取 消</el-button>
            <el-button type="primary" @click="confirm('formValue')">确 定</el-button>
        </div>
        </el-dialog>
</template>
    
<script>
import tableMixins from '../../mixins/tableMixin'
export default {
    name: '',
    mixins: [tableMixins],
    props:[
        'visible','inputVal'
    ],
    data() {
        return {
            formValue:{
                id:'',
                money:''
            },//表单数据
            dormData:[],
            rules: {
                money: [
                    { required: true, message: '金额不能为空', trigger: 'blur' },
                ],
            }
        }
    },
    // computed: {
        
    // },
    created() {
        
    },
    mounted(){
        
    },
    watch:{
        inputVal(){
            console.log('inputVal发生了变化',this.inputVal);
            this.formValue.id = this.inputVal.id
        },
    },
    methods: {
        /* 确定 */
        confirm(formName){
            const _this = this
            // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
            let time = new Date().toLocaleString()
            // console.log(time);
            _this.$refs[formName].validate((valid) => {
                if (valid) {
                    _this.add({
                        url: '/student/topup',
                        data: {
                            stuId: _this.formValue.id, 
                            money: Number(_this.formValue.money),
                            // time:time
                        },
                        updateUrl:'/student/getstudent',
                        callback:function(data) {
                            _this.$emit('updatemyData',data)
                            _this.delDialog()
                        }
                    })
                }
            });
            
        },
 
        /* 跟新确定 */
        updateQueding(row) {
            this.updateState = false
            this.update({
                url: '/suShe/gengXinById',
                data: this.formUpdate,
                method: 'post'
            })
        },
         /* 取消弹框 */
        delDialog(){
            this.formValue.id = ''
            this.formValue.money = ''
            this.$emit('closeDialog')
        },
    
    },
   
}
</script>
