<template>
  <div class="content" v-if="isupdate">
    <!-- 添加 -->
    <StudentModal :type="'add'" :visible="showModal" @closeDialog='mycloseDialog'></StudentModal>
    <!-- 修改 -->
    <StudentModal :type="'update'" :visible="showModal" :inputVal="changeUpdate" @closeDialog='mycloseDialog' @updatemyData='updateData'></StudentModal>
    <!-- 缴费记录 -->
    <PayMentModal :visible="showPayMentModal" :inputVal="changeUpdate" @closeDialog='myclosePayDialog'></PayMentModal>
    <!-- 充值 -->
    <TopupModal :visible="showTopupModal" :inputVal="changeUpdate" @closeDialog='mycloseTopDialog' @updatemyData='updateData'></TopupModal>
    
    <!-- 搜索 -->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="searchVal" placeholder="请输入学生姓名"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSearch" :disabled="!userInfo.type">搜索</el-button>
        <el-button type="primary" @click="reset" :disabled="!userInfo.type">重置</el-button>
      </el-form-item>
      <el-form-item>
        <el-button class="addUser" type="primary" @click="showModal = true" :disabled="!userInfo.type">添加学生</el-button>
      </el-form-item>
    </el-form>

    <!-- 表格展示 -->
    <el-table :data="myCurrentData" border style="width: 100%;margin:3rem auto;">
      <el-table-column align="center" fixed prop="stuId" label="学号">
      </el-table-column>
      <el-table-column align="center" prop="stuName" label="姓名">
      </el-table-column>
      <el-table-column align="center" prop="stuUserId" label="账号">
      </el-table-column>
      <!-- <el-table-column align="center" prop="stuPas" label="密码">
      </el-table-column> -->
      <el-table-column align="center" prop="dormId" label="学生所属宿舍">
      </el-table-column>
      <el-table-column align="center" prop="balance" label="宿舍水电费余额（元）">
      </el-table-column>
      <el-table-column align="center" prop="type" label="宿舍状态">
        <template slot-scope="scope">
          <p>{{scope.row.type == 1 ? '正常' : '催款中'}}</p>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width="250px">
        <template slot-scope="scope">
          <el-button @click="delQueding(scope.row)" type="text" size="small" v-if="userInfo.type">删除</el-button>
          <el-button @click="updateForm(scope.row)" type="text" size="small" v-if="userInfo.type">修改</el-button>
          <el-button @click="getPayMent(scope.row)" type="text" size="small">查看缴费记录</el-button>
          <el-button @click="topup(scope.row)" type="text" size="small" v-if="!userInfo.type">充值</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- pageCount 总页码  current-change 改变当前页 -->
    <el-pagination center layout="prev, pager, next" :current-page="currentPage" :page-count="myPageCount" @current-change="myhandleCurrentChange">
    </el-pagination>

  </div>
</template>

<script>
import tableMixins from '../mixins/tableMixin'
import StudentModal from '../components/modal/StudentModal'
import PayMentModal from '../components/modal/PayMentModal'
import TopupModal from '../components/modal/TopupModal'
import { getStorage } from '../utils/changeSessionStorage'
export default {
    name: '',
    components: {
        StudentModal,PayMentModal,TopupModal
    },
    mixins: [tableMixins],
    data() {
        return {
            userInfo:'',//登录的用户信息
            selectUrl: '/suShe/chaXunAll',
            myTableData:[],//表格总数据
            currentPage: 1,/* 当前页 */
            pageNum: 5,/* 每页显示数据条数 */
            showModal:false,//是否显示修改增加弹框
            showPayMentModal:false,//是否显示缴费记录弹框
            showTopupModal:false,//是否显示充值弹框
            changeUpdate:'',//点击修改时的那一条数据
            isupdate:true,
            searchVal:'',//搜索框内容
        }
    },
    computed:{
        myCurrentData() {
        // 　　console.log('currentData重新执行',this.myTableData);
            let qishi = (this.currentPage - 1) * this.pageNum
            let jieshu = qishi + this.pageNum
            return this.myTableData.slice(qishi, jieshu)
        },
        /* 总页码  一共多少页 */
        myPageCount() {
            // console.log('重新渲染页数');
            return Math.ceil(this.myTableData.length / this.pageNum)
        }
    },
    created() {
        const _this = this
        this.userInfo = getStorage('userInfo')
        this.onSelect({
            url:'/student/getstudent'
        },(data)=>{
            // console.log('渲染时获取的data',data);
            if (!this.userInfo.type) {
                console.log('判断',this.userInfo.id);
                this.myTableData = data.filter(item=>{
                    return item.id == this.userInfo.id
                })
            } else {
                this.myTableData = data
            }
        })
    },
    mounted(){
        
    },
    methods: {
        myhandleCurrentChange(val) {
            // console.log('page发生改变');
            this.currentPage = val
        },
        /* 添加确定 */
        addQueding() {
            this.add({
                url: '/suShe/xinZeng',
                data: this.formAdd
            })
        },
        /* 删除按钮 */
        delQueding(row) {
            const _this = this
            _this.$confirm('确认删除该学生吗', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    console.log('row.id',row.id);
                    _this.del({
                        url: '/student/delstudent',
                        data: { id: row.id },
                        updateUrl:'/student/getstudent',
                        callback:function(data) {
                          _this.updateData(data)
                        }
                    })
                }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        /* 点击修改 */
        updateForm(item){
            console.log('item',item);
            this.changeUpdate = item
            this.showModal = true
        },
        /* 关闭增加修改弹框 */
        mycloseDialog(){
            // console.log('传过来的取消弹框');
            this.showModal = false
            this.changeUpdate = ''
            // this.$forceUpdate();
            // this.isupdate = false
            // this.$nextTick(() => {
            //     this.isupdate = true
            // })
        },
        /* 关闭缴费记录 */
        myclosePayDialog(){
            this.showPayMentModal = false
            this.changeUpdate = ''
        },
        /* 关闭充值弹框 */
        mycloseTopDialog(){
            this.showTopupModal = false
            this.changeUpdate = ''
        },
        /* 更新data数据 */
        updateData(data){
            // console.log('我的data',data);
            if (!this.userInfo.type) {
                console.log('判断',this.userInfo.id);
                this.myTableData = data.filter(item=>{
                    return item.id == this.userInfo.id
                })
            } else {
                this.myTableData = data
            }
            // this.myTableData = data
            this.currentPage = 1
        },
        /* 搜索 */
        onSearch(){
            console.log(this.searchVal);
            if (this.searchVal === '') {
                this.onSelect({
                    url:'/student/getstudent'
                },(data)=>{
                    this.myTableData = data
                })
            } else {
                this.onSelect({
                    url:'/student/getsinglestudent',
                    params:{
                        stuName:this.searchVal
                    }
                },(data)=>{
                    this.myTableData = data
                })
            }
            this.currentPage = 1
        },
        /* 重置 */
        reset(){
            this.searchVal = ''
            this.onSelect({
            url:'/student/getstudent'
            },(data)=>{
                if (!this.userInfo.type) {
                    console.log('判断',this.userInfo.id);
                    this.myTableData = data.filter(item=>{
                        return item.id == this.userInfo.id
                    })
                } else {
                    this.myTableData = data
                }
            })
        },
        /* 催款按钮 */
        dept(row) {
            const _this = this
            let type = ''
            if (row.type == 1) {
                type = 2
            } else {
                type = 1
            }
            _this.$confirm('确认催款该宿舍吗', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                _this.del({
                    url: '/dorm/deptdorm',
                    data: { 
                        id: row.id,
                        type:type
                    },
                    updateUrl:'/dorm/getdorm',
                    callback:function(data) {
                        _this.updateData(data)
                    }
                })
            }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: '已取消催款'
                });          
            });
        },
        /* 查看缴费记录 */
        getPayMent(item){
            this.changeUpdate = item
            this.showPayMentModal = true
        },
        /* 查看缴费记录 */
        topup(item){
            this.changeUpdate = item
            this.showTopupModal = true
        },
    }
}
</script>

<style scoped lang="less">
.addUser {
  background-color: #fff;
  color: #07c4a8;
  border-radius: 4px;
  border: 1px solid #07c4a8;
}
.content {
  margin-top: 2rem;
}
.el-pagination {
  text-align: center;
}
</style>
