<template>
    <el-dialog title="缴费记录" :visible.sync="visible" @close='delDialog'>
        <el-table :data="myCurrentData" center>
            <el-table-column align="center" property="money" label="缴费金额" ></el-table-column>
            <el-table-column align="center" property="mytime" label="缴费时间"></el-table-column>
            <!-- <el-table-column property="address" label="地址"></el-table-column> -->
        </el-table>
        <el-pagination center layout="prev, pager, next" :current-page="currentPage" :page-count="myPageCount" @current-change="myhandleCurrentChange">
        </el-pagination>
</el-dialog>
</template>
    
<script>
import tableMixins from '../../mixins/tableMixin'
export default {
    name: 'PayMentModal',
    mixins: [tableMixins],
    props:[
        'visible','inputVal'
    ],
    data() {
        return {
            tableData:[],
            id:'',//查询人的id
            currentPage: 1,/* 当前页 */
            pageNum: 5,/* 每页显示数据条数 */
        }
    },
    computed:{
        myCurrentData() {
        // 　　console.log('currentData重新执行',this.myTableData);
            let qishi = (this.currentPage - 1) * this.pageNum
            let jieshu = qishi + this.pageNum
            return this.tableData.slice(qishi, jieshu)
        },
        /* 总页码  一共多少页 */
        myPageCount() {
            // console.log('重新渲染页数');
            return Math.ceil(this.tableData.length / this.pageNum)
        }
    },
    watch:{
        inputVal(){
            console.log('inputVal发生了变化',this.inputVal);
            this.id = this.inputVal.id
            this.onSelect({
                url:'/student/getstudentpayment',
                params:{
                    id:this.id
                }
            },(data)=>{
                this.myTableData = data
                // console.log(new Date(data[1].time).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'') );
            })
        },
    },
    mounted(){
        
    },
    methods: {
        myhandleCurrentChange(val) {
            // console.log('page发生改变');
            this.currentPage = val
        },
         /* 取消弹框 */
        delDialog(){
            // console.log('取消弹框');
            this.tableData = [],
            this.id = '',
            this.$emit('closeDialog')
            this.currentPage = 1
        },
    },
   
}
</script>